/* Base Imports and Variables */

:root {
  /* Original Colors */
  --bg-primary: #111827;
  --bg-secondary: #1F2937;
  --text-primary: #F3F4F6;
  --text-secondary: #9CA3AF;
  --accent: #3B82F6;
  --accent-hover: #2563EB;

  /* Enhanced Shadow System */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);

  /* Spacing System */
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-6: 1.5rem;
  --space-8: 2rem;

  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-full: 9999px;

  /* Transitions */
  --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--bg-primary);
  color: var(--text-primary);
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/* Enhanced Container */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: var(--space-8);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  grid-gap: var(--space-4);
  gap: var(--space-4);
  padding-bottom: calc(var(--space-8) + 60px); /* Space for bottom menu */
}

/* Enhanced Event Card */
.event-card {
  background-color: var(--bg-secondary);
  border-radius: var(--radius-lg);
  padding: var(--space-6);
  margin-bottom: var(--space-4);
  box-shadow: var(--shadow-md);
  transition: transform var(--transition-normal), box-shadow var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.event-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Enhanced Team Logos Section */
.team-logos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: var(--space-6);
  padding: var(--space-4);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-lg);
}

.team-logo {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: var(--radius-full);
  background-position: center;
  background-size: cover;
  border: 2px solid var(--accent);
  transition: transform var(--transition-normal), border-color var(--transition-normal);
  box-shadow: var(--shadow-sm);
}

.team-logo:hover {
  transform: scale(1.1);
  border-color: var(--accent-hover);
}

/* Enhanced Action Buttons */
.action-btn {
  flex: 1 1;
  padding: var(--space-3) var(--space-4);
  border: none;
  border-radius: var(--radius-md);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.action-btn::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-full);
  transform: translate(-50%, -50%);
  transition: width 0.6s ease, height 0.6s ease;
}

.action-btn:active::after {
  width: 400px;
  height: 400px;
}

/* Enhanced Bottom Menu */
.bottom-menu {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(31, 41, 55, 0.95);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  display: flex;
  justify-content: space-around;
  padding: var(--space-4);
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.bottom-menu button {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  font-weight: 600;
  transition: all var(--transition-normal);
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-md);
  position: relative;
}

.bottom-menu button::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--accent);
  transition: all var(--transition-normal);
  transform: translateX(-50%);
}

.bottom-menu button:hover::after,
.bottom-menu button.active::after {
  width: 70%;
}

/* Enhanced Form Elements */
input, select {
  width: 100%;
  padding: var(--space-3);
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--text-primary);
  border-radius: var(--radius-md);
  transition: all var(--transition-normal);
}

input:hover, select:hover {
  background-color: rgba(255, 255, 255, 0.08);
}

input:focus, select:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
  outline: none;
}

/* Enhanced Modal */
.modal-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.75);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn var(--transition-normal);
}

.modal-content {
  background-color: var(--bg-secondary);
  padding: var(--space-8);
  border-radius: var(--radius-lg);
  max-width: 90%;
