  box-shadow: var(--shadow-lg);
  animation: modalSlide var(--transition-normal);
}

/* Enhanced Loading States */
@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.loading {
  background: linear-gradient(
          90deg,
