.profile-container {
    width: 100%;
    max-width: 64rem;
    margin: 0 auto;
    padding: 1rem;
    direction: rtl;
}

/* Card Styles */
.profile-card {
    background: white;
    border-radius: 0.75rem;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    margin-bottom: 1.5rem;
}

/* Header Section */
.profile-header {
    padding: 1.5rem;
    background: linear-gradient(to right, #2563eb, #1d4ed8);
    border-radius: 0.75rem 0.75rem 0 0;
    color: white;
}

.profile-main-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.profile-name-section {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

.profile-nickname {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 0;
}

.edit-button {
    background: none;
    border: none;
    color: #93c5fd;
    cursor: pointer;
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    transition: color 0.2s;
}

.edit-button:hover {
    color: white;
}

/* Edit Mode */
.edit-mode-container {
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.edit-input {
    padding: 0.5rem;
    border-radius: 0.25rem;
    border: 1px solid #e5e7eb;
    font-size: 1rem;
}

.save-button,
.cancel-button {
    padding: 0.5rem 1rem;
    border-radius: 0.25rem;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
}

.save-button {
    background-color: #22c55e;
    color: white;
}

.save-button:hover {
    background-color: #16a34a;
}

.cancel-button {
    background-color: #6b7280;
    color: white;
}

.cancel-button:hover {
    background-color: #4b5563;
}

/* League Selection */
.league-select {
    padding: 0.5rem;
    border-radius: 0.25rem;
    border: 1px solid #e5e7eb;
    background-color: white;
    margin-right: 1rem;
}

/* Stats Section */
.stats-section {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 1rem;
    background-color: rgba(255, 255, 255, 0.1);
}

.stat-item {
    color: #e5e7eb;
}

/* Personal Info Section */
.personal-info {
    padding: 1.5rem;
}

.section-title {
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 1rem;
    color: #1f2937;
}

.info-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.info-item {
    display: flex;
    flex-direction: column;
    grid-gap: 0.25rem;
    gap: 0.25rem;
}

.info-label {
    color: #6b7280;
    font-size: 0.875rem;
}

.info-value {
    color: #111827;
    font-size: 1rem;
}

/* Bet History Section */
.bet-history {
    padding: 1.5rem;
}

.history-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
}

.history-table th {
    background-color: #f3f4f6;
    color: #4b5563;
    font-weight: 600;
    padding: 0.75rem;
    text-align: right;
}

.history-table tr {
    transition: background-color 0.2s;
}

.history-table tr:hover {
    background-color: #f9fafb;
}

.history-table td {
    padding: 0.75rem;
    border-bottom: 1px solid #e5e7eb;
}

.result-win {
    color: #22c55e;
    font-weight: 600;
}

.result-loss {
    color: #ef4444;
    font-weight: 600;
}

.points-positive {
    color: #22c55e;
    font-weight: 600;
}

.points-negative {
    color: #ef4444;
    font-weight: 600;
}

/* Responsive Design */
@media (max-width: 768px) {
    .profile-container {
        padding: 0.5rem;
    }

    .info-grid {
        grid-template-columns: 1fr;
    }

    .profile-main-info {
        flex-direction: column;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .stats-section {
        flex-direction: column;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .history-table {
        font-size: 0.875rem;
    }

    .history-table th,
    .history-table td {
        padding: 0.5rem;
    }
}

/* Loading States */
.shimmer {
    background: linear-gradient(
            90deg,
            #f3f4f6 0%,
            #e5e7eb 50%,
            #f3f4f6 100%
    );
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 200% 0;
    }
}
/* Container Styles */
.rules-container {
    width: 100%;
    max-width: 64rem; /* 4xl */
    margin: 0 auto;
    padding: 1rem;
    direction: rtl;
}

/* Header Styles */
.rules-header {
    background: linear-gradient(to right, #2563eb, #1e40af);
    color: white;
    border-radius: 0.5rem 0.5rem 0 0;
    padding: 1.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.rules-header h1 {
    font-size: 1.875rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.rules-header p {
    color: #bfdbfe;
}

/* Rules Content */
.rules-content {
    background: white;
    border-radius: 0 0 0.5rem 0.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Section Styles */
.rules-section {
    border-bottom: 1px solid #e5e7eb;
}

.rules-section:last-child {
    border-bottom: none;
}

/* Section Button */
.section-button {
    width: 100%;
    padding: 1rem 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: none;
    border: none;
    cursor: pointer;
    transition: background-color 0.15s;
}

.section-button:hover {
    background-color: #f9fafb;
}

.section-header {
    display: flex;
    align-items: center;
    grid-gap: 0.75rem;
    gap: 0.75rem;
}

.section-icon {
    font-size: 1.5rem;
}

.section-title {
    font-size: 1.25rem;
    font-weight: 600;
}

/* Arrow Icon */
.arrow-icon {
    transition: transform 0.2s;
}

.arrow-icon.expanded {
    transform: rotate(180deg);
}

/* Section Content */
.section-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-out;
    background-color: #f9fafb;
}

.section-content.expanded {
    max-height: 24rem; /* adjust based on content */
}

.section-content-inner {
    padding: 1rem 1.5rem;
}

/* Rules List */
.rules-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.rules-list li {
    display: flex;
    align-items: flex-start;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-bottom: 0.75rem;
}

.rules-list li::before {
    content: "•";
    color: #2563eb;
    margin-top: 0.25rem;
}

/* Quick Navigation */
.quick-nav {
    margin-top: 1.5rem;
    background: white;
    border-radius: 0.5rem;
    padding: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.quick-nav h3 {
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 0.75rem;
}

.quick-nav-buttons {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.quick-nav-button {
    padding: 0.5rem 1rem;
    border-radius: 9999px;
    background-color: #f3f4f6;
    border: none;
    cursor: pointer;
    transition: all 0.15s;
}

.quick-nav-button:hover {
    background-color: #e5e7eb;
}

.quick-nav-button.active {
    background-color: #2563eb;
    color: white;
}

/* Responsive Design */
@media (max-width: 640px) {
    .rules-container {
        padding: 0.5rem;
    }

    .rules-header {
        padding: 1rem;
    }

    .rules-header h1 {
        font-size: 1.5rem;
    }

    .section-button {
        padding: 0.75rem 1rem;
    }

    .section-title {
        font-size: 1.125rem;
    }

    .quick-nav-buttons {
        flex-direction: column;
    }

    .quick-nav-button {
        width: 100%;
        text-align: center;
    }
}
